let renderEntireTree = (state) => {
    ReactDOM.render(
        <React.StrictMode>
            <App
                state={state}
                dispatch3={store.dispatch3.bind(store)}
                dispatch4={store.dispatch4.bind(store)}
                dispatch5={store.dispatch5.bind(store)}
                dispatch6={store.dispatch6.bind(store)}
            />
        </React.StrictMode>,
        document.getElementById('root')
      );
};

renderEntireTree(store.getState());
store.subscribe(renderEntireTree);
