let strQuestion =`
import React from 'react';
import s from './Question.module.css';
import Circle from './Circle';
import NumberQueastion from './NumberQueastion';
import TextQueastion from './TextQueastion';
import { AnswerType, StateType } from '../../../redus/store';

type PropsType = {
    circleElement: Array<AnswerType>,
    text: string,
    index: number
    getGo: ()=>boolean,
    getQuantity: ()=>number,
    countTrue: number,
    countFalse:number
}

const Question: React.FC<PropsType>  = (props) => {
    return (
        <div className={s.mainQuestion}>
            <NumberQueastion index={props.index} getQuantity={props.getQuantity}/>
            <Circle
                circleElement={props.circleElement}
                countTrue = {props.countTrue}
                countFalse = {props.countFalse}
            />
            {props.getGo()
                ? <TextQueastion text={props.text} index={props.index}/>
                : <></>
            }

        </div>
    )
}

export default Question;
`;


export default strQuestion;
