import React, { useEffect, useState } from 'react';
import cc from '../../ColorCode.module.css';
import vsc from '../../ProtoVsc.module.css';
import {APP_CSS, APP_TSX, CIRCLE, CONTROL, CONTROL_CSS, FOLDER_COMPONENTS, FOLDER_CONTROL, FOLDER_HEADER, FOLDER_HOOK, FOLDER_OPTIONS, FOLDER_QUESTION, FOLDER_REDUS, HEADER, HEADER_CSS, HOOK, INDEX_CSS, INDEX_TSX, INTRO, LOGO, MAININTRO, NUMBERQUEASTION, OPTIONS, OPTIONS_CSS, QUESTION, QUESTION_CSS, REACT_APP_ENV_D, STORE_TS, TEXTQUEASTION, TITLE, TSCONFIG_JSON} from '../../redux/constName'
import Folder from './Folder';
import File from './File';

const ContentExplorer = (props) => {
    let [status, setStatus] = useState(props.state)

    let className = vsc.explorerHide;

    const unwrap = () => { //–¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ –≤—ã–µ–∑–¥–∞ —Å–ª–µ–≤–∞ –Ω–∞ –º–∞–ª–æ–º —ç–∫—Ä–∞–Ω–µ
        switch (props.id) {
            case 3:
                if(status.step_3.showExplorer){
                    className = vsc.explorerShow;
                } else {
                    className = vsc.explorerHide;
                }
                break;
            case 4:
                if(status.step_4.showExplorer){
                    className = vsc.explorerShow;
                } else {
                    className = vsc.explorerHide;
                }
                break;
            case 5:
                 if(status.step_5.showExplorer){
                    className = vsc.explorerShow;
                } else {
                    className = vsc.explorerHide;
                }
                break;
            case 6:
                if(status.step_6.showExplorer){
                    className = vsc.explorerShow;
                } else {
                    className = vsc.explorerHide;
                }
                break;
            default:
                // console.log('–Ω–µ—Ç props.id');
        }
    }
    unwrap()

    useEffect( ()  => {
        setStatus(props.state);
    }, [props.state])
    return (
        <div className= {`${vsc.explorer} ${cc.colorWhite} ${className}`}>
        <div className={`${vsc.explorer__header} ${vsc.colorFileName} ${vsc.font14}`}><code>explorer</code></div>
            <div className={vsc.container}>
                <div className={vsc.explorer__column}>
                    <div className={`${vsc.explorer__row } ${cc.colorBlocked}`}>
                        <div className= {`${vsc.folderBracket} ${vsc.offsetExpl} ${vsc.font14} ${vsc.cursorPointer}`}></div>
                        <div className={`${vsc.folder} ${vsc.offsetExpl} ${vsc.font14}`}></div>
                        <div className={`${vsc.folderName} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14}`}> src </div>
                    </div>
                    <div className={vsc.row__sub} >
                        <div className={`${vsc.explorer__row } ${cc.colorBlocked}`}>
                            <div className= {`${vsc.folderBracket} ${vsc.offsetExpl} ${vsc.font14} ${vsc.cursorPointer}`}></div>
                            <div className={`${vsc.folder} ${vsc.offsetExpl} ${vsc.font14}`}></div>
                            <div className={`${vsc.folderName} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14}`}> assets </div>
                        </div>
                        {props.id < 6 &&
                            <div className={`${vsc.explorer__row} ${cc.colorBlocked}`}>
                                <div className= {`${vsc.folderBracket} ${vsc.offsetExpl} ${vsc.font14} ${vsc.cursorPointer}`}></div>
                                <div className={`${vsc.folder} ${vsc.offsetExpl} ${vsc.font14}`}></div>
                                <div className={`${vsc.folderName} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14}`}> components </div>
                            </div>
                        }
                        {props.id >= 6 &&
                            <>
                                <Folder folderName={FOLDER_COMPONENTS} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={true}>
                                        <Folder folderName={FOLDER_CONTROL} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={true}>
                                            <File fileName={CONTROL_CSS} className={vsc.fileCss} dispatch={props.dispatch}
                                                state={props.state} enabled={true} id={props.id}/>
                                            <File fileName={CONTROL} className={vsc.fileTS} dispatch={props.dispatch}
                                                state={props.state} enabled={true} id={props.id}/>
                                        </Folder>
                                    <Folder folderName={FOLDER_HEADER} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={true} >
                                        <Folder folderName={FOLDER_QUESTION} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={true}>
                                                <File fileName={CIRCLE} className={vsc.fileTS} dispatch={props.dispatch}
                                                    state={props.state} enabled={true} id={props.id}/>
                                                <File fileName={NUMBERQUEASTION} className={vsc.fileTS} dispatch={props.dispatch}
                                                    state={props.state} enabled={true} id={props.id}/>
                                                <File fileName={QUESTION_CSS} className={vsc.fileCss} dispatch={props.dispatch}
                                                    state={props.state} enabled={true} id={props.id}/>
                                                <File fileName={QUESTION} className={vsc.fileTS} dispatch={props.dispatch}
                                                    state={props.state} enabled={true} id={props.id}/>
                                                <File fileName={TEXTQUEASTION} className={vsc.fileTS} dispatch={props.dispatch}
                                                    state={props.state} enabled={true} id={props.id}/>
                                        </Folder>
                                        <File fileName={HEADER_CSS} className={vsc.fileCss} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                                        <File fileName={HEADER} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                                        <File fileName={INTRO} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                                        <File fileName={LOGO} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                                        <File fileName={MAININTRO} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                                        <File fileName={TITLE} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                                    </Folder>
                                    <Folder folderName={FOLDER_OPTIONS} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={true}>
                                        <File fileName={OPTIONS_CSS} className={vsc.fileCss} dispatch={props.dispatch}
                                            state={props.state} enabled={true} id={props.id}/>
                                        <File fileName={OPTIONS} className={vsc.fileTS} dispatch={props.dispatch}
                                            state={props.state} enabled={true} id={props.id}/>
                                    </Folder>
                                </Folder>
                                <Folder folderName={FOLDER_HOOK} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={true}>
                                    <File fileName={HOOK} className={vsc.fileTS} dispatch={props.dispatch}
                                        state={props.state} enabled={true} id={props.id}/>
                                </Folder>
                            </>
                        }
                        {props.id < 6 &&
                            <div className={`${vsc.explorer__row} ${cc.colorBlocked}`}>
                                <div className= {`${vsc.folderBracket} ${vsc.offsetExpl} ${vsc.font14} ${vsc.cursorPointer}`}></div>
                                <div className={`${vsc.folder} ${vsc.offsetExpl} ${vsc.font14}`}></div>
                                <div className={`${vsc.folderName} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14}`}> Hook </div>
                            </div>
                        }
                        {props.id === 3 &&
                            <Folder folderName={FOLDER_REDUS} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={false}>
                                <File fileName={STORE_TS} className={vsc.fileTS} dispatch={props.dispatch}
                                    state={props.state} enabled={true} id={props.id}/>
                            </Folder>
                        }
                        {props.id > 3 && props.id < 6 &&
                            <Folder folderName={FOLDER_REDUS} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={true}>
                                <File fileName={STORE_TS} className={vsc.fileTS} dispatch={props.dispatch}
                                    state={props.state} enabled={true} id={props.id}/>
                            </Folder>
                        }
                        {props.id >= 6 &&
                            <Folder folderName={FOLDER_REDUS} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={true}>
                                <File fileName={STORE_TS} className={vsc.fileTS} dispatch={props.dispatch}
                                    state={props.state} enabled={true} id={props.id}/>
                            </Folder>
                        }
                        {/* //–ù–ê–ß–ê–õ–û///////5///////// */}
                        {props.id < 5 &&
                            <>
                                <File fileName={APP_CSS} className={vsc.fileCss} dispatch={props.dispatch} state={props.state} enabled={false} id={props.id}/>
                                <File fileName={APP_TSX} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={false} id={props.id}/>
                            </>
                        }
                        {props.id >= 5 &&
                        <>
                            <File fileName={APP_CSS} className={vsc.fileCss} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                            <File fileName={APP_TSX} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                        </>
                        }
                        {/* ////–ö–û–ù–ï–¶/////5///////// */}
                        {props.id < 4 &&
                            <>
                                <File fileName={INDEX_CSS} className={vsc.fileCss} dispatch={props.dispatch} state={props.state} enabled={false} id={props.id}/>
                                <File fileName={INDEX_TSX} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={false} id={props.id}/>
                            </>
                        }
                        {props.id >= 4 &&
                            <>
                                <File fileName={INDEX_CSS} className={vsc.fileCss} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                                <File fileName={INDEX_TSX} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                            </>
                        }
                        {props.id >= 6 &&
                            <>
                                <File fileName={REACT_APP_ENV_D} className={vsc.fileTSEvnD} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                            </>
                        }
                    </div>
                    {props.id >= 3 &&
                            <>
                                <File fileName={TSCONFIG_JSON} className={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                            </>
                        }
                </div>
            </div>
        </div>
    )

}

export default ContentExplorer;
