let strQuestion =`
.mainQuestion {
    padding: 2% 2% 0 2%;
}
.mainQuestion__text {
    margin-top: 1%;
}
.circle {
    display: flex;
    gap: 1%;
}
.circle__target {
    border-radius:50%;
    width: 24px;
    height: 24px;
    background-color: white;
    border:1px solid black;
}
.circleGadget {
    /* display: none; */
    display: flex;
    justify-content: space-around;
}
.circleGadget__target:nth-child(1) {
    display: flex;
    justify-content: center;
    border-radius:50%;
    width: 30px;
    height: 30px;
    background-color: #00B28E;
    border:1px solid black;
    align-content: center;
    flex-wrap: wrap;
}
.circleGadget__target:nth-child(2) {
    display: flex;
    justify-content: center;
    border-radius:50%;
    width: 30px;
    height: 3030;
    background-color: red;
    border:1px solid black;
    align-content: center;
    flex-wrap: wrap;
}
.true  {
    background-color: #00B28E;
}
.false  {
    background-color: red;
}

.mainQuestion__quantity {
    margin-bottom: 1%;
}

`;


export default strQuestion;
