let strOptions =`
import React, { ChangeEvent } from 'react';
import s from './Options.module.css';
import { useEffect  } from 'react';
import { ShuffleType, StateType, VarAnswerType } from '../../redus/store';

type PropsType = {
    state: StateType,
    shuffleOptions: () => ShuffleType,
    getGo: () => boolean,
    versionAnswer: (answer: VarAnswerType) => void

}

function Options (props: PropsType): React.ReactNode {

    let variant = props.shuffleOptions();
    const addResponse = (e:ChangeEvent<HTMLInputElement>) => {
        let textAnswer = e.target.value;
        let newAnswer = {
            id: props.state.count,
            text: textAnswer
        }
        props.versionAnswer(newAnswer);
    }

    useEffect( ()  => {
       if ( props.getGo() ) {

            let inputOptions: NodeListOf<HTMLInputElement> = document.querySelectorAll(".question-options");
            for (let i =0; i < inputOptions.length; i++) {
                if (inputOptions[i].checked) {
                    inputOptions[i].checked = false;
                }
            }
        }
    })

    let variantMap = variant.text.map((v, index) =>
        <li key={index}>
            <input className="question-options"
                id={'question_' + index}
                name='question' type="radio"
                onChange={addResponse}
                value={v} />
            <label className={s.answer} htmlFor={'question_' + index}>{v}</label>
        </li>
    )
    if( props.getGo()) {
        return (
            <ul className={s.answers}>
                {variantMap}
            </ul>

        )
    } else {
        return (<div></div>)
    }
}
export default Options;
`;

export default strOptions;
