const SHOW_EXPLORER__STAPE = 'SHOW_EXPLORER__STAPE'
const INITIALIZATION_STAPE = 'INITIALIZATION_STAPE';

const UPDATE_FILE_STORE_STEP = 'SEND_STORE_STEP';
const UPDATE_FILE_TSCONFIG_STEP = 'UPDATE_FILE_TSCONFIG_STEP';

const UPDATE_FILE_INDEX_STAPE = 'UPDATE_FILE_INDEX_STAPE';
const UPDATE_FILE_INDEX_CSS_STAPE = 'UPDATE_FILE_INDEX_CSS_STAPE';

const UPDATE_FILE_APP = 'UPDATE_FILE_APP';
const UPDATE_FILE_APP_CSS = 'UPDATE_FILE_APP_CSS';

const UPDATE_FILE_REACT_APP_ENV_D_TS = 'UPDATE_FILE_REACT_APP_ENV_D_TS';
const UPDATE_FILE_HEADER = 'UPDATE_FILE_HEADER';
const UPDATE_FILE_HEADER_CSS = 'UPDATE_FILE_HEADER_CSS';
const UPDATE_FILE_QUESTION = 'UPDATE_FILE_QUESTION';
const UPDATE_FILE_QUESTION_CSS = 'UPDATE_FILE_QUESTION_CSS';
const UPDATE_FILE_CIRCLE = 'UPDATE_FILE_CIRCLE';
const UPDATE_FILE_NUMBERQUEASTION = 'UPDATE_FILE_NUMBERQUEASTION';
const UPDATE_FILE_TEXTQUEASTION  = 'UPDATE_FILE_TEXTQUEASTION';
const UPDATE_FILE_INTRO  = 'UPDATE_FILE_INTRO';
const UPDATE_FILE_LOGO  = 'UPDATE_FILE_LOGO';
const UPDATE_FILE_MAININTRO  = 'UPDATE_FILE_MAININTRO';
const UPDATE_FILE_TITLE  = 'UPDATE_FILE_TITLE';
const UPDATE_FILE_CONTROL_CSS  = 'UPDATE_FILE_CONTROL_CSS';
const UPDATE_FILE_CONTROL  = 'UPDATE_FILE_CONTROL';
const UPDATE_FILE_OPTIONS  = 'UPDATE_FILE_OPTIONS';
const UPDATE_FILE_OPTIONS_CSS  = 'UPDATE_FILE_OPTIONS_CSS';
const UPDATE_FILE_HOOK  = 'UPDATE_FILE_HOOK';





const reducer = (status, action) => {

    switch (action.type) {
        case SHOW_EXPLORER__STAPE:
            status.showExplorer = action.flag;
            return status;
        case UPDATE_FILE_STORE_STEP:
            status.statusStore = action.flag;
            return status;
        case UPDATE_FILE_TSCONFIG_STEP:
            status.statusTSConfig = action.flag;
            return status;
        case UPDATE_FILE_INDEX_STAPE:
            status.statusIndex = action.flag;
            return status;
        case UPDATE_FILE_INDEX_CSS_STAPE:
            status.statusIndexCss = action.flag;
            return status;
        case UPDATE_FILE_APP:
            status.statusApp = action.flag;
            return status;
        case UPDATE_FILE_APP_CSS:
            status.statusAppCss = action.flag;
            return status;
        case UPDATE_FILE_REACT_APP_ENV_D_TS:
            status.statusAppEnvDTs  = action.flag;
            return status;
        case UPDATE_FILE_HEADER:
            status.statusHeader  = action.flag;
            return status;
        case UPDATE_FILE_HEADER_CSS:
            status.statusHeaderCss  = action.flag;
            return status;
        case UPDATE_FILE_QUESTION:
            status.statusQuestion = action.flag;
            return status;
        case UPDATE_FILE_QUESTION_CSS:
            status.statusQuestionCss = action.flag;
            return status;
        case UPDATE_FILE_CIRCLE:
            status.statusCircle = action.flag;
            return status;
        case UPDATE_FILE_NUMBERQUEASTION:
            status.statusNumberQueastion = action.flag;
            return status;
        case UPDATE_FILE_TEXTQUEASTION:
            status.statusTextQueastion = action.flag;
            return status;
        case UPDATE_FILE_INTRO:
            status.statusIntro  = action.flag;
            return status;
        case UPDATE_FILE_LOGO:
            status.statusLogo = action.flag;
            return status;
        case UPDATE_FILE_MAININTRO:
            status.statusMainIntro = action.flag;
            return status;
        case UPDATE_FILE_TITLE:
            status.statusTitle = action.flag;
            return status;
        case UPDATE_FILE_CONTROL:
            status.statusControl = action.flag;
            return status;
        case UPDATE_FILE_CONTROL_CSS:
            status.statusControlCss = action.flag;
            return status;
        case UPDATE_FILE_OPTIONS:
            status.statusOptions = action.flag;
            return status;
        case UPDATE_FILE_OPTIONS_CSS:
            status.statusOptionsCss = action.flag;
            return status;
        case UPDATE_FILE_HOOK:
            status.statusHook = action.flag;
            return status;


        case INITIALIZATION_STAPE:
            status.statusStore = false;
            status.statusTSConfig = false;
            status.statusIndex = false;
            status.statusIndexCss = false;
            status.statusApp = false;
            status.statusAppCss = false;
            status.statusAppEnvDTs = false;
            status.statusHeader  = false;
            status.statusHeaderCss  = false;

            status.statusQuestion = false;
            status.statusQuestionCss = false;
            status.statusCircle = false;
            status.statusNumberQueastion = false;
            status.statusTextQueastion = false;
            status.statusIntro = false;
            status.statusLogo = false;
            status.statusMainIntro = false;
            status.statusTitle = false;
            status.statusControl = false;
            status.statusControlCss = false;
            status.statusOptions= false;
            status.statusOptionsCss= false;
            status.statusHook= false;

            return status;
        default:
            return status;
    }
}

export const updateStoreStape6 = (body) => ({ type:  UPDATE_FILE_STORE_STEP, flag: body});
export const updateTSConfigStape6 = (body) => ({ type: UPDATE_FILE_TSCONFIG_STEP, flag: body});
export const updateIndexStape6 = (body) => ({ type: UPDATE_FILE_INDEX_STAPE, flag: body});
export const updateIndexCssStape6 = (body) => ({ type: UPDATE_FILE_INDEX_CSS_STAPE, flag: body});
export const updateAppStape6 = (body) => ({ type: UPDATE_FILE_APP, flag: body});
export const updateAppCssStape6 = (body) => ({ type: UPDATE_FILE_APP_CSS, flag: body});
export const updateAppEnvDTs6 = (body) => ({ type: UPDATE_FILE_REACT_APP_ENV_D_TS, flag: body});
export const updateHeader6 = (body) => ({ type: UPDATE_FILE_HEADER, flag: body});
export const updateHeaderCss6 = (body) => ({ type: UPDATE_FILE_HEADER_CSS, flag: body});

export const updateQuestion = (body) => ({ type: UPDATE_FILE_QUESTION, flag: body});
export const updateQuestionCss = (body) => ({ type: UPDATE_FILE_QUESTION_CSS, flag: body});
export const updateCircle = (body) => ({ type: UPDATE_FILE_CIRCLE, flag: body});
export const updateNumberQueastion = (body) => ({ type: UPDATE_FILE_NUMBERQUEASTION, flag: body});
export const updateTextQueastion = (body) => ({ type: UPDATE_FILE_TEXTQUEASTION, flag: body});
export const updateIntro = (body) => ({ type: UPDATE_FILE_INTRO, flag: body});
export const updateLogo = (body) => ({ type: UPDATE_FILE_LOGO, flag: body});
export const updateMainIntro = (body) => ({ type: UPDATE_FILE_MAININTRO, flag: body});
export const updateTitle = (body) => ({ type: UPDATE_FILE_TITLE, flag: body});
export const updateControl = (body) => ({ type: UPDATE_FILE_CONTROL, flag: body});
export const updateControlCss = (body) => ({ type: UPDATE_FILE_CONTROL_CSS, flag: body});
export const updateOptions = (body) => ({ type: UPDATE_FILE_OPTIONS, flag: body});
export const updateOptionsCss = (body) => ({ type: UPDATE_FILE_OPTIONS_CSS, flag: body});
export const updateHook = (body) => ({ type: UPDATE_FILE_HOOK, flag: body});


export const initializationStape6 = () => ({ type: INITIALIZATION_STAPE });
export const showExplorerStape6 = (body) => ({ type: SHOW_EXPLORER__STAPE, flag: body});



export default reducer;
