import React, { useState } from 'react';
import vsc from '../../../ProtoVsc.module.css';
import { showExplorerStape3 } from '../../../redux/reduser3';
import { showExplorerStape4 } from '../../../redux/reduser4';
import { showExplorerStape5 } from '../../../redux/reduser5';
import { showExplorerStape6 } from '../../../redux/reduser6';



const Navbar = (props) => {
	let [active, setActive] = useState(false);
	let [firstDownload, setFirstDownload] = useState(true);
	// debugger
	const click = () => {
        if(active) {
            setActive(false);

			unwrap(false);
			console.log(1);
        } else {
			setActive(true);
			unwrap(true);
			console.log(2);
			setFirstDownload(false);
		}
    }

	const unwrap = (bool) => {
        switch (props.id) {
            case 3 :
				// debugger
				console.log(bool);
				console.log('props.state= '+ props.state.step_3.showExplorer);
				props.dispatch(showExplorerStape3(bool));
				console.log('props.state= '+ props.state);
                break;
            case 4:
                props.dispatch(showExplorerStape4(bool));
                break;
            case 5:
                props.dispatch(showExplorerStape5(bool));
                break;
			case 6:
				props.dispatch(showExplorerStape6(bool));
				break;
            default:
                console.log('–Ω–µ—Ç props.id');
        }
    }

	return (
		<>
			{/* —á—Ç–æ–±—ã –Ω–µ –ø–æ–∫–∞–∑—ã–≤–∞–ª–∞—Å—å –∞–Ω–∏–º–∞—Ü–∏—è –ø—Ä–∏ –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∫–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã */}
			{!active && firstDownload &&
				<div className={vsc.vabar} onClick={click}>
					<button type="button" className={`${vsc.navbarToggle} ${vsc.rsMenuForm__burger}`} >
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
					</button>
				</div>
			}
			{active && !firstDownload &&
				<div className={vsc.vabar} onClick={click}>
					<button type="button" className={`${vsc.navbarToggle} ${vsc.rsMenuForm__burger} ${vsc.active}`} >
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
					</button>
				</div>
			}
			{!active && !firstDownload &&
				<div className={vsc.header} onClick={click}>
					<button type="button" className={`${vsc.navbarToggle} ${vsc.rsMenuForm__burger} ${vsc.notActive}`} >
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
					</button>
				</div>
			}

		</>


	)
}

export default Navbar;
