import React, { useEffect, useState } from 'react';
import cc from '../../ColorCode.module.css';
import vsc from '../../ProtoVsc.module.css';
import { PrismAsyncLight as SyntaxHighlighter } from 'react-syntax-highlighter'
import { oneDark } from 'react-syntax-highlighter/dist/esm/styles/prism';

async function recomputeCode(generator) {
    let code;
    try {
      code = await generator;
    } catch (error) {
      console.error('There was an error in your code generator: ', error);
      const unknownCode = 'Nothing to see here, move along!';
      return unknownCode;
    }
    return code;
}

const ContentValue = (props) => {
    const [copy, setCopy] = useState(true);
    const [code, setCode] = useState('');

    const onClick–°lipboard = () => {
        navigator.clipboard.writeText(props.str);
        setCopy(false);
        setTimeout(() => {
            setCopy(true);
        },1000 )
    }

    let isDown = props.status;
    useEffect(
        () => {
          async function debounce() {
            if(!isDown) {
                setCode('');
            } else {
              setCode(await recomputeCode(props.str));
            }
          }
          debounce();
        }
    );

    return (
        <>
            {isDown &&
                <div className= {`${cc.colorPurple}  ${vsc.windowContent} ${vsc.offsetWind} ${vsc.font14}`}>
                    <div className={`${cc.colorPurple}  ${vsc.font14} ${vsc.containerSyntaxhigHlighter} ${vsc.containerPositionAbsolute}`} >
                        <SyntaxHighlighter language="jsx" style={oneDark} wrapLongLines={true}>
                            {code}
                        </SyntaxHighlighter>
                    </div>
                    <div className={vsc.containerHiaderButton}>
                    {copy ? (
                        <button className= {vsc.iconClipbord} onClick={onClick–°lipboard} >Copy code</button>
                        ) : (
                        <button className= {vsc.iconCheck}>Copyed</button>
                        )}
                    </div>
                </div>
            }
        </>
    )

}

export default ContentValue;
