let strOptionsCss =`
.answers {
    padding: 2%;
    font-size: 1.1em ;
    /* font-family: 'Commissioner', sans-serif; */
    font-weight: 500 ;
}
@media  (max-width: 400px) {
    .answers {
		font-size: 1.1em;
	}
}
ul.answers li {
    list-style-type: none;
}
ul.answers li input, ul.answers li label  {
    cursor: pointer;
}
.answer {
    margin-left: 1%;
}
`;

export default strOptionsCss;
