import React, { useState } from 'react';
import cc from '../../ColorCode.module.css';
import vsc from '../../ProtoVsc.module.css';
import {initializationStape3} from '../../redux/reduser3'
import { initializationStape4 } from '../../redux/reduser4';
import { initializationStape5 } from '../../redux/reduser5';
import { initializationStape6 } from '../../redux/reduser6';


const Folder = (props) => {
    let [isDown, setEditDown] = useState(false);
    let [isFirstDownload, setFirstDownload] = useState(props.isFirstDownload);
    // debugger
    const dropUp = () => {
        setEditDown(true);
        switch(props.id) {
            case 3:
                props.dispatch(initializationStape3());
                break;
            case 4:
                props.dispatch(initializationStape4());
                break;
            case 5:
                props.dispatch(initializationStape5());
                break;
            case 6:
                props.dispatch(initializationStape6());
                break;
            default:
                // console.log('–Ω–µ—Ç props.id');
                break;
        }
    }
    const drop = () => {
        setFirstDownload(false);
        if(isDown) {
            setEditDown(false);
        }
    }

    return (
        <>
            {isFirstDownload &&
                <div className={vsc.explorer__column } onClick={drop}>
                    <div className={`${vsc.explorer__row} ${cc.cursorPointer}`}>
                        <code className= {`${vsc.folderBracket} ${vsc.offsetExpl} ${vsc.font14} ${vsc.cursorPointer}`}></code>
                        <code className={`${vsc.folder} ${vsc.offsetExpl} ${vsc.font14}`} ></code>
                        <code className={`${vsc.folderName} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14} ${vsc.cursorPointer}`}> {props.folderName} </code>
                    </div>
                </div>
            }
            {isDown && props.enabled && !isFirstDownload &&
                <div className={vsc.explorer__column } onClick={drop}>
                    <div className={`${vsc.explorer__row} ${cc.cursorPointer}`}>
                        <code className= {`${vsc.folderBracket} ${vsc.offsetExpl} ${vsc.font14} ${vsc.cursorPointer} ${vsc.rotate0}`}></code>
                        <code className={`${vsc.folder} ${vsc.offsetExpl} ${vsc.font14}`} ></code>
                        <code className={`${vsc.folderName} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14} ${vsc.cursorPointer}`}> {props.folderName} </code>
                    </div>
                </div>
            }
            {!isDown && props.enabled && !isFirstDownload &&
                <div className={vsc.explorer__column} >
                    <div className={`${vsc.explorer__row} ${cc.cursorPointer}`} onClick={dropUp}>
                        <code className= {`${vsc.folderBracket} ${vsc.offsetExpl} ${vsc.font14} ${vsc.cursorPointer} ${vsc.rotate90} `}></code>
                        <code className={`${vsc.folder} ${vsc.offsetExpl} ${vsc.font14} ${vsc.cursorPointer}`}></code>
                        <code className={`${vsc.folderName} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14} ${vsc.cursorPointer}`}> {props.folderName} </code>
                    </div>
                    <div className={`${vsc.row__subFile} ${vsc.font14} ${vsc.cursorPointer}`}>
                        {props.children}
                    </div>
                </div>
            }
            {!props.enabled && isFirstDownload &&
                <div className={`${vsc.explorer__row} ${cc.colorBlocked}`}>
                    <div className= {`${vsc.folderBracket} ${vsc.offsetExpl} ${vsc.font14}`}></div>
                    <div className={`${vsc.folder} ${vsc.offsetExpl} ${vsc.font14}`}></div>
                    <div className={`${vsc.folderName} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14}`}> {props.folderName} </div>
                </div>
            }
        </>
    )

}

export default Folder;
