import React, { useEffect, useState } from 'react';
import vsc from '../../ProtoVsc.module.css';
import cc from '../../ColorCode.module.css';
import ContentValue from './ContentValue';
import {updateStoreStape3} from '../../redux/reduser3';
import {updateIndexStape4} from '../../redux/reduser4';
import {updateAppStape5} from '../../redux/reduser5';
import { updateStoreStape6 } from '../../redux/reduser6';


const ContentWindow = (props) => {
    const [initDidMount, setInitDidMount] = useState(true);
    const init = () => {
      if (initDidMount) {
          try {
            props.id===3 &&
            props.dispatch(updateStoreStape3(true));
          } catch (error) { }
          try {
            props.id===4 &&
            props.dispatch(updateIndexStape4(true));
          } catch (error) { }
          try {
            props.id===5 &&
            props.dispatch(updateAppStape5(true));
          } catch (error) { }
          try {
            props.id===6 &&
            props.dispatch(updateStoreStape6(true));
          } catch (error) { }

        }
        setInitDidMount(false)
    };

    useEffect(
      () => {
        init();
      }
    );

    return (
        <>
          <div className= {`${vsc.window} ${cc.colorWhite} `} >
						<div className={`${vsc.header__window } ${cc.colorAzuree} ${cc.colorWhite} ${vsc.font14}`}><code>window</code></div>
            {props.id===3 && props.state.step_3.statusStore &&
              <ContentValue
                status={props.state.step_3.statusStore}
                str={props.strFial.strRedus}
              />
            }
            {props.id===3 && props.state.step_3.statusTSConfig &&
              <ContentValue
                status={props.state.step_3.statusTSConfig}
                str={props.strFial.strTsconfig}
              />
            }

            {/* 4 */}
            {props.id===4 && props.state.step_4.statusStore &&
              <ContentValue
                status={props.state.step_4.statusStore}
                str={props.strFial.strRedus}
              />
            }
            {props.id===4 && props.state.step_4.statusTSConfig &&
              <ContentValue
                status={props.state.step_4.statusTSConfig}
                str={props.strFial.strTsconfig}
              />
            }
            {props.id===4 && props.state.step_4.statusIndex &&
              <ContentValue
                status={props.state.step_4.statusIndex}
                str={props.strFial.strIndexJs}
              />
            }
            {props.id===4 && props.state.step_4.statusIndexCss &&
              <ContentValue
                status={props.state.step_4.statusIndexCss}
                str={props.strFial.strIndexCss}
              />
            }
            {/* 5 */}
            {props.id===5 && props.state.step_5.statusStore &&
              <ContentValue
                status={props.state.step_5.statusStore}
                str={props.strFial.strRedus}
              />
            }
            {props.id===5 && props.state.step_5.statusTSConfig &&
              <ContentValue
                status={props.state.step_5.statusTSConfig}
                str={props.strFial.strTsconfig}
              />
            }
            {props.id===5 && props.state.step_5.statusIndex &&
              <ContentValue
                status={props.state.step_4.statusIndex}
                str={props.strFial.strIndexJs}
              />
            }
            {props.id===5 && props.state.step_5.statusIndexCss &&
              <ContentValue
                status={props.state.step_5.statusIndexCss}
                str={props.strFial.strIndexCss}
              />
            }
            {props.id===5 && props.state.step_5.statusApp &&
              <ContentValue
                status={props.state.step_5.statusApp}
                str={props.strFial.strApp}
              />
            }
            {props.id===5 && props.state.step_5.statusAppCss &&
              <ContentValue
                status={props.state.step_5.statusAppCss}
                str={props.strFial.strAppCss}
              />
            }
            {/* 6 */}
            {props.id===6 && props.state.step_6.statusStore &&
              <ContentValue
                status={props.state.step_6.statusStore}
                str={props.strFial.strRedus}
              />
            }
            {props.id===6 && props.state.step_6.statusTSConfig &&
              <ContentValue
                status={props.state.step_6.statusTSConfig}
                str={props.strFial.strTsconfig}
              />
            }
            {props.id===6 && props.state.step_6.statusIndex &&
              <ContentValue
                status={props.state.step_4.statusIndex}
                str={props.strFial.strIndexJs}
              />
            }
            {props.id===6 && props.state.step_6.statusIndexCss &&
              <ContentValue
                status={props.state.step_6.statusIndexCss}
                str={props.strFial.strIndexCss}
              />
            }
            {props.id===6 && props.state.step_6.statusApp &&
              <ContentValue
                status={props.state.step_6.statusApp}
                str={props.strFial.strApp}
              />
            }
            {props.id===6 && props.state.step_6.statusAppCss &&
              <ContentValue
                status={props.state.step_6.statusAppCss}
                str={props.strFial.strAppCss}
              />
            }
            {props.id===6 && props.state.step_6.statusAppEnvDTs &&
              <ContentValue
                status={props.state.step_6.statusAppEnvDTs}
                str={props.strFial.strAppEnvDTs}
              />
            }
            {props.id===6 && props.state.step_6.statusHeader &&
              <ContentValue
                status={props.state.step_6.statusHeader}
                str={props.strFial.strHeader}
              />
            }
            {props.id===6 && props.state.step_6.statusHeaderCss &&
              <ContentValue
                status={props.state.step_6.statusHeaderCss}
                str={props.strFial.strHeaderCss}
              />
            }
            {props.id===6 && props.state.step_6.statusQuestion &&
              <ContentValue
                status={props.state.step_6.statusQuestion}
                str={props.strFial.strQuestion}
              />
            }
            {props.id===6 && props.state.step_6.statusQuestionCss &&
              <ContentValue
                status={props.state.step_6.statusQuestionCss}
                str={props.strFial.strQuestionCss}
              />
            }
            {props.id===6 && props.state.step_6.statusNumberQueastion &&
              <ContentValue
                status={props.state.step_6.statusNumberQueastion}
                str={props.strFial.strNumberQueastion}
              />
            }
            {props.id===6 && props.state.step_6.statusTextQueastion &&
              <ContentValue
                status={props.state.step_6.statusTextQueastion}
                str={props.strFial.strTextQueastion}
              />
            }
            {props.id===6 && props.state.step_6.statusIntro &&
              <ContentValue
                status={props.state.step_6.statusIntro}
                str={props.strFial.strIntro}
              />
            }
            {props.id===6 && props.state.step_6.statusLogo &&
              <ContentValue
                status={props.state.step_6.statusLogo}
                str={props.strFial.strLogo}
              />
            }
            {props.id===6 && props.state.step_6.statusMainIntro &&
              <ContentValue
                status={props.state.step_6.statusMainIntro}
                str={props.strFial.strMainIntro}
              />
            }
            {props.id===6 && props.state.step_6.statusTitle &&
              <ContentValue
                status={props.state.step_6.statusTitle}
                str={props.strFial.strTitle}
              />
            }
            {props.id===6 && props.state.step_6.statusCircle &&
              <ContentValue
                status={props.state.step_6.statusCircle}
                str={props.strFial.strCircle}
              />
            }
            {props.id===6 && props.state.step_6.statusControl &&
              <ContentValue
                status={props.state.step_6.statusControl}
                str={props.strFial.strControl}
              />
            }
            {props.id===6 && props.state.step_6.statusControlCss &&
              <ContentValue
                status={props.state.step_6.statusControlCss}
                str={props.strFial.strControlCss}
              />
            }
            {props.id===6 && props.state.step_6.statusOptionsCss &&
              <ContentValue
                status={props.state.step_6.statusOptionsCss}
                str={props.strFial.strOptionsCss}
              />
            }
            {props.id===6 && props.state.step_6.statusOptions &&
              <ContentValue
                status={props.state.step_6.statusOptions}
                str={props.strFial.strOptions}
              />
            }
            {props.id===6 && props.state.step_6.statusHook &&
              <ContentValue
                status={props.state.step_6.statusHook}
                str={props.strFial.strHook}
              />
            }
					</div>
        </>
    )

  }

  export default ContentWindow;
