let strIndexJs =`
import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import {StateType, addAnswer, getGo, getQuantity, getState, shuffleOptions, subscribe, versionAnswer } from './redus/store';

const root=ReactDOM.createRoot(document.getElementById('root') as HTMLElement);
let renderEntireTree=(props: StateType) => {
	root.render(
		<App state={props}
			versionAnswer={versionAnswer}
			addAnswer={addAnswer}
			getGo={getGo}
			shuffleOptions={shuffleOptions}
			getQuantity={getQuantity}
		/>
	);
}
renderEntireTree(getState());
subscribe(renderEntireTree);
`;

export default strIndexJs;
