let strControl =`
import React from 'react';
import s from './Control.module.css';

type PropsType = {
    getGo: () => boolean,
    addAnswer: () => void
}

function  Control (props: PropsType): React.ReactNode {
    const onwards = () => {
        props.addAnswer();
    }
    return (
        <div>
            { props.getGo()
                ? <button className={s.nextQuestion} onClick={onwards} >–î–∞–ª—å—à–µ ¬ª</button>
                : <button disabled className={\u0060\u0024{s.nextQuestion} \u0024{s.nextQuestionDisabled}\u0060} > –í—Å—ë </button>
            }
        </div>
    )
}
export default Control;
`;

export default strControl;
