let strApp =`
import './App.css';
import Header from './components/Header/Header';
import Options from './components/Options/Options';
import Control from './components/Control/Control';
import { StateType, VarAnswerType, ShuffleType} from './redus/store';

type PropsType = {
    state: StateType,
    getGo: () => boolean,
    getQuantity: () => number,
    versionAnswer: (answer: VarAnswerType) => void,
    shuffleOptions: () => ShuffleType,
    addAnswer: () => void
}

const App: React.FC<PropsType>=(props)=>{
    return (
        <div className = "appWrapper">
            <Header state={props.state} getGo={props.getGo} getQuantity={props.getQuantity}/>
            <Options state={props.state} versionAnswer={props.versionAnswer} getGo={props.getGo} shuffleOptions={props.shuffleOptions}/>
            <Control addAnswer={props.addAnswer} getGo={props.getGo}/>
        </div>
    );
  };
  export default App;
`;

export default strApp;
