import reducer3 from './reduser3';
import reducer4 from './reduser4';
import reducer5 from './reduser5';
import reducer6 from './reduser6';

let store = {

    _state: {
        step_3: {
            showExplorer: false,
            statusStore: false,
            statusTSConfig: false,
        },
        step_4: {
            showExplorer: false,
            statusStore: false,
            statusTSConfig: false,
            statusIndex: false,
            statusIndexCss: false
        },
        step_5: {
            showExplorer: false,
            statusStore: false,
            statusTSConfig: false,
            statusIndex: false,
            statusIndexCss: false,
            statusApp: false,
            statusAppCss: false
        },
        step_6: {
            showExplorer: false,
            statusStore: false,
            statusTSConfig: false,
            statusIndex: false,
            statusIndexCss: false,
            statusApp: false,
            statusAppCss: false,
            statusHeader: false,
            statusHeaderCss: false,
            statusQuestion: false,
            statusQuestionCss: false,
            statusCircle: false,
            statusNumberQueastion: false,
            statusTextQueastion: false,
            statusIntro: false,
            statusLogo: false,
            statusMainIntro: false,
            statusTitle: false,
            statusControl: false,
            statusControlCss: false,
            statusOptions: false,
            statusOptionsCss: false,
            statusHook: false
        }

        // statusHeader: false,
        // statusHeaderCss: false,
    },
    getState() {
        return this._state;
    },
    _renderEntireTree() {
        console.log('–í—ã–∑–æ–≤ state');
    },
    subscribe (observer){
        this._renderEntireTree = observer;
    },
    dispatch3 (action) {
        this._state.step_3 = reducer3(this._state.step_3, action);
        this._renderEntireTree(this._state);
    },
    dispatch4 (action) {
        this._state.step_4 = reducer4(this._state.step_4, action);
        this._renderEntireTree(this._state);
    },
    dispatch5 (action) {
        this._state.step_5 = reducer5(this._state.step_5, action)
        this._renderEntireTree(this._state);
    },
    dispatch6 (action) {
        this._state.step_6 = reducer6(this._state.step_6, action)
        this._renderEntireTree(this._state);
    }
};


window.store = store;

export default store;
