import React from 'react';
import cc from '../../ColorCode.module.css';
import vsc from '../../ProtoVsc.module.css';
import {initializationStape3, updateTSConfigStape3, updateStoreStape3} from '../../redux/reduser3'
import {updateIndexStape4, initializationStape4, updateStoreStape4, updateTSConfigStape4, updateIndexCssStape4} from '../../redux/reduser4'
import {INDEX_CSS, INDEX_TSX, TSCONFIG_JSON, STORE_TS, APP_CSS, APP_TSX, REACT_APP_ENV_D, HEADER, HEADER_CSS, QUESTION, QUESTION_CSS, CIRCLE, NUMBERQUEASTION, TEXTQUEASTION, INTRO, LOGO, MAININTRO, TITLE, CONTROL, CONTROL_CSS, OPTIONS_CSS, OPTIONS, HOOK} from '../../redux/constName'
import { initializationStape5, updateAppCssStape5, updateAppStape5, updateIndexCssStape5, updateIndexStape5, updateStoreStape5, updateTSConfigStape5 } from '../../redux/reduser5';
import { initializationStape6, updateAppCssStape6, updateAppEnvDTs6, updateAppStape6, updateCircle, updateControl, updateControlCss, updateHeader6, updateHeaderCss6, updateHook, updateIndexCssStape6, updateIndexStape6, updateIntro, updateLogo, updateMainIntro, updateNumberQueastion, updateOptions, updateOptionsCss, updateQuestion, updateQuestionCss, updateStoreStape6, updateTextQueastion, updateTitle, updateTSConfigStape6 } from '../../redux/reduser6';


const File = (props) => {

    const unwrap = () => {
        switch (props.id) {
            case 3 :
                switch (props.fileName) {
                    case STORE_TS:
                        if (!props.state.step_3.statusStore) {
                            props.dispatch(initializationStape3());
                            props.dispatch(updateStoreStape3(true));
                        }
                    break;
                    case  TSCONFIG_JSON:
                        if (!props.state.step_3.statusTSConfig) {
                            props.dispatch(initializationStape3());
                            props.dispatch(updateTSConfigStape3(true));
                        }
                    break;
                    default:
                        console.log('–Ω–µ—Ç props.fileName');
                }
                break;
            case 4:
                switch (props.fileName) {
                    case INDEX_TSX:
                        if (!props.state.step_4.statusIndex) {
                            props.dispatch(initializationStape4());
                            props.dispatch(updateIndexStape4(true));
                        }
                    break;
                    case INDEX_CSS:
                        if (!props.state.step_4.statusIndexCss) {
                            props.dispatch(initializationStape4());
                            props.dispatch(updateIndexCssStape4(true));
                        }
                    break;
                    case STORE_TS:
                        if (!props.state.step_4.statusStore) {
                            props.dispatch(initializationStape4());
                            props.dispatch(updateStoreStape4(true));
                        }
                    break;
                    case TSCONFIG_JSON:
                        if (!props.state.step_4.statusTSConfig) {
                            props.dispatch(initializationStape4());
                            props.dispatch(updateTSConfigStape4(true));
                        }
                    break;
                    default:
                        console.log('–Ω–µ—Ç props.fileName');
                }
                break;
            case 5:
                switch (props.fileName) {
                    case INDEX_TSX:
                        if (!props.state.step_5.statusIndex) {
                            props.dispatch(initializationStape5());
                            props.dispatch(updateIndexStape5(true));
                        }
                    break;
                    case INDEX_CSS:
                        if (!props.state.step_5.statusIndexCss) {
                            props.dispatch(initializationStape5());
                            props.dispatch(updateIndexCssStape5(true));
                        }
                    break;
                    case STORE_TS:
                        if (!props.state.step_5.statusStore) {
                            props.dispatch(initializationStape5());
                            props.dispatch(updateStoreStape5(true));
                        }
                    break;
                    case TSCONFIG_JSON:
                        if (!props.state.step_5.statusTSConfig) {
                            props.dispatch(initializationStape5());
                            props.dispatch(updateTSConfigStape5(true));
                        }
                    break;
                    case APP_TSX:
                        if (!props.state.step_5.statusTSConfig) {
                            props.dispatch(initializationStape5());
                            props.dispatch(updateAppStape5(true));
                        }
                    break;
                    case APP_CSS:
                        if (!props.state.step_5.statusTSConfig) {
                            props.dispatch(initializationStape5());
                            props.dispatch(updateAppCssStape5(true));
                        }
                    break;
                    default:
                        console.log('–Ω–µ—Ç props.fileName');
                }
                break;
            case 6:
                switch (props.fileName) {
                    case INDEX_TSX:
                        if (!props.state.step_6.statusIndex) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateIndexStape6(true));
                        }
                    break;
                    case INDEX_CSS:
                        if (!props.state.step_6.statusIndexCss) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateIndexCssStape6(true));
                        }
                    break;
                    case STORE_TS:
                        if (!props.state.step_6.statusStore) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateStoreStape6(true));
                        }
                    break;
                    case TSCONFIG_JSON:
                        if (!props.state.step_6.statusTSConfig) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateTSConfigStape6(true));
                        }
                    break;
                    case APP_TSX:
                        if (!props.state.step_6.statusTSConfig) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateAppStape6(true));
                        }
                    break;
                    case APP_CSS:
                        if (!props.state.step_6.statusTSConfig) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateAppCssStape6(true));
                        }
                    break;
                    case REACT_APP_ENV_D:
                        if (!props.state.step_6.statusAppEnvDTs) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateAppEnvDTs6(true));
                        }
                    break;
                    case HEADER:
                        if (!props.state.step_6.statusHeader) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateHeader6(true));
                        }
                    break;
                    case HEADER_CSS:
                        if (!props.state.step_6.statusHeaderCss) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateHeaderCss6(true));
                        }
                    break;
                    case QUESTION:
                        if (!props.state.step_6.statusQuestion) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateQuestion(true));
                        }
                    break;
                    case QUESTION_CSS:
                        if (!props.state.step_6.statusQuestionCss) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateQuestionCss(true));
                        }
                    break;
                    case CIRCLE:
                        if (!props.state.step_6.statusCircle) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateCircle(true));
                        }
                    break;
                    case NUMBERQUEASTION:
                        if (!props.state.step_6.statusNumberQueastion) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateNumberQueastion(true));
                        }
                    break;
                    case TEXTQUEASTION:
                        if (!props.state.step_6.statusTextQueastion) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateTextQueastion(true));
                        }
                    break;
                    case INTRO:
                        if (!props.state.step_6.statusIntro) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateIntro(true));
                        }
                    break;
                    case LOGO:
                        if (!props.state.step_6.statusLogo) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateLogo(true));
                        }
                    break;
                    case MAININTRO:
                        if (!props.state.step_6.statusMainIntro) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateMainIntro(true));
                        }
                    break;
                    case TITLE:
                        if (!props.state.step_6.statusTitle) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateTitle(true));
                        }
                    break;
                    case CONTROL:
                        if (!props.state.step_6.statusControl) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateControl(true));
                        }
                    break;
                    case CONTROL_CSS:
                        if (!props.state.step_6.statusControlCss) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateControlCss(true));
                        }
                    break;
                    case OPTIONS_CSS:
                        if (!props.state.step_6.statusOptionsCss) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateOptionsCss(true));
                        }
                    break;
                    case OPTIONS:
                        if (!props.state.step_6.statusOptions) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateOptions(true));
                        }
                    break;
                    case HOOK:
                        if (!props.state.step_6.statusHook) {
                            props.dispatch(initializationStape6());
                            props.dispatch(updateHook(true));
                        }
                    break;
                    default:
                        // console.log('–Ω–µ—Ç props.fileName');
                }
                break;
            default:
                console.log('–Ω–µ—Ç props.id');
        }
    }
    return (
        <>
            <div className={`${vsc.row__files} ${!props.enabled && `${cc.colorBlocked}`}`} onClick={unwrap}>
                <div className={`${props.className} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14} ${vsc.cursorPointer}`}>
                    <code>{props.fileName}</code>
                </div>
            </div>

        </>
    )
}

export default File;
